<template>
    <div id="xhgequ">
        <h1 id="ge">歌曲 {{likeshus}}</h1>
        <h1 id="sp" @click="$router.push('/YonHu2')">视频 {{likeshus2}}</h1>
        <div id="xian"></div>
        <h1 id="yy">歌曲</h1>
        <h1 id="zz">歌手</h1>

        <div id="fan">
            <div v-for="yy in yinyue" :key="yy.xhid">
                <div class="bianliyy">
                    <img :src=" '/api/yytp/' +yy.mtp" class="yytp">
                    <h1 class="blyy" @click="$router.push('/KanYinyue/' + yy.mid)">{{yy.mname.substring(0,yy.mname.indexOf('.'))}}</h1>
                    <h1 class="blzz">{{yy.mauthor}}</h1>
                    <div class="ycbf">
                        <audio :src=" '/api/yinyue/' + yy.mname " controls="controls" class="buof"></audio>
                    </div>
                    <button type="button" class="scan" @click="deleteComment(yy.mid,yh.id)">X</button>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
    export default {
        name: "YonHulikeyy",
        data() {
            return {
                yinyue: [],
                yh: {},
                likeshus: {},
                likeshus2: {}
            }
        },
        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    // console.log(resp);
                    // console.log(resp.data.data);
                    this.yh = resp.data.data;
                    this.bianliyy();
                    this.likeshu();
                    this.likeshu2();
                });
            },
            bianliyy() {
                console.log(this.yh.id);
                this.$axios({
                    url: "/api/yhlkyy?id=" + this.yh.id
                }).then(r => {
                    console.log('好好好好');
                    console.log("222", r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu(){
                this.$axios({
                    url: "/api/yhlikeyyshu?id=" + this.yh.id
                }).then(r => {
                    console.log(r);
                    this.likeshus = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu2(){
                this.$axios({
                    url: "/api/yhlikespshu?id=" + this.yh.id
                }).then(r => {
                    console.log(r);
                    this.likeshus2 = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            deleteComment(id,yhid) {
                this.$confirm("确定要取消喜欢吗?")
                    .then(() => {
                        this.$axios({
                            url: '/api/yhsclkyy?mid=' + id+'&id='+ yhid
                        }).then(r => {
                            this.$message("移除成功!");
                            this.bianliyy();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },

        },
        created() {
            this.doLogin2();
        }
    }
</script>

