<template>

    <div id="kuan">
        <el-carousel :interval="4000" type="card" height="270px">
            <el-carousel-item v-for="yy in yinyue" :key="yy.mid">

                <template v-if="yhsf==null">
                    <template v-if="yy.mvip==1">
                        <img :src=" '/api/yytp/' +yy.mtp" class="yytu" @click="tisi">
                        <div class="tsvip" v-if="yy.mvip==1">vip</div>
                    </template>
                    <template v-else>
                        <img :src=" '/api/yytp/' +yy.mtp" class="yytu" @click="$router.push('/KanYinyue/' + yy.mid)">
                        <div class="tsvip" v-if="yy.mvip==1">vip</div>
                    </template>
                </template>

                <template v-else>
                    <template v-if="(yy.mvip==1&&yhsfvip==1)||(yy.mvip==0&&yhsfvip==1)||(yy.mvip==0&&yhsfvip==0)">
                        <img :src=" '/api/yytp/' +yy.mtp" class="yytu" @click="$router.push('/KanYinyue/' + yy.mid)">
                        <div class="tsvip" v-if="yy.mvip==1">vip</div>
                    </template>
                    <template v-if="yy.mvip==1&&yhsfvip==0">
                        <img :src=" '/api/yytp/' +yy.mtp" class="yytu" @click="$router.push('/vip')">-->
                        <div class="tsvip" v-if="yy.mvip==1">vip</div>
                    </template>
                </template>
<!--                <img :src=" '/api/yytp/' +yy.mtp" class="yytu" @click="$router.push('/KanYinyue/' + yy.mid)">-->
<!--                <div class="tsvip" v-if="yy.mvip==1">vip</div>-->
            </el-carousel-item>
        </el-carousel>
    </div>

</template>

<script>
    export default {
        name: "lunbuo",
        data() {
            return {
                yinyue: [],
                yh: {},
                yhsfvip: {},
                yhsf: '有呀'
            }
        },
        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/lun",
                }).then(r => {
                    console.log(r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            this.loadPosts();
            this.doLogin2();
        }
    }
</script>

<style scoped>
    .yytu{
        width: 666px;
        height: 278px;
    }
    .tsvip{
        width: 30px;
        background-color: green;
        position: absolute;
        top: 0px;
        left: 0px;
        color: white;
        border-radius: 3px;
        opacity: 0.8;
    }
    #kuan{
        height: 310px;
        width: 90%;
        padding-top: 80px;
        background-color: #F6F6F6;
        margin-top: 0px;
        margin-left: 75px;
        margin-bottom: 0px;
        /*margin-left: 80px;*/
        /*width: 88%;*/
    }
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
</style>