<template>
    <div>
        <hands></hands>
        <handtiao3></handtiao3>

        <div id="czgszhuti">

            <img :src=" '/api/tp/bg_singer.jpg' " id="toutp">
            <input type="" name="" id="ss" value="" v-model="namelikes" placeholder="    搜索歌手..."/>
            <img :src=" '/api/tp/QQ截图20210106094850.png'" @click="like" id="ssan">
            <img :src=" '/api/tp/QQ截图20210106104608.png'" @click="$router.push('idnex')" id="an2">
            <input type="button" name="" id="an3" value="返回全部" @click="loadPosts"/>

            <div id="yyk">
                <div class="yykuai" v-for="sp in gesou" :key="sp.vid">
                    <video :src="'/api/shipin/'+sp.vname" class="yytp"></video>
                    <span class="yyname">{{sp.vname.substring(0,sp.vname.indexOf('.'))}}</span><br>
                    <span class="yyzz">{{sp.vauthor}}</span>
<!--                    <div class="buofan" @click="$router.push('KanShipin/'+sp.vid)">▶</div>-->

                    <template v-if="yhsf==null">
                        <template v-if="sp.vvip==1">
                            <div class="buofan"  @click="tisi">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                        <template v-else>
                            <div class="buofan"  @click="$router.push('/KanShipin/' + sp.vid)">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                    </template>

                    <template v-else>
                        <template v-if="(sp.vvip==1&&yhsfvip==1)||(sp.vvip==0&&yhsfvip==1)||(sp.vvip==0&&yhsfvip==0)">
                            <div class="buofan"  @click="$router.push('/KanShipin/' + sp.vid)">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                        <template v-if="sp.vvip==1&&yhsfvip==0">
                            <div class="buofan"  @click="$router.push('/vip')">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                    </template>

                </div>

                <el-pagination
                        background
                        layout="prev, pager, next"
                        @current-change="handlePageChange"
                        :current-page="page.current"
                        :page-size="page.size"
                        :total="page.total" id="tiao"
                        style="margin-top: 650px;margin-left: 550px">
                </el-pagination><!--第几页、每页多少条、总条数-->
            </div>

        </div>
        <yhfankui></yhfankui>
        <fanhuidinbu></fanhuidinbu>
    </div>
</template>

<script>
    import hands from '../components/hands';
    import handtiao3 from '../comfuyon2/handtiao3';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "cazaoshipin",
        components: {
            hands, handtiao3, yhfankui, fanhuidinbu
        },
        data() {
            return {
                page: {
                    current: 1,//第几页
                    size: 15,   //每页的数量
                    total: 0    //数据量
                },
                gesou: [],
                namelikes: '',
                yh: {},
                yhsfvip: {},
                yhsf: '有呀'
            }
        },
        methods: {
            loadPosts() {
                this.$axios({
                    url: "/api/kansp",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.gesou = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szsp",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.gesou = resp.data.data;
                });
            },

            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message('亲，你还没有登录哦~');
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            this.loadPosts();
            this.doLogin2();
        }
    }
</script>

<style scoped>
    .tsvip{
        width: 25px;
        background-color: green;
        position: absolute;
        top: 0px;
        left: 0px;
        color: white;
        border-radius: 3px;
        opacity: 0.8;
    }
    #czgszhuti{
        width: 100%;
        height: 1000px;
        position: relative;
        /* background-color: red; */
        background-color: #F6F6F6;
        margin-top: 40px;
    }
    #toutp{
        width: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
    }
    #ss{
        width: 440px;
        height: 45px;
        position: absolute;
        top: 95px;
        left: 440px;
        border-radius: 5px;
        font-size: 17px;
    }
    #ssan{
        width: 40px;
        height: 40px;
        position: absolute;
        top: 100px;
        left: 848px;
    }
    #ssan:hover{
        cursor: pointer;
    }
    #an2{
        width: 40px;
        height: 40px;
        position: absolute;
        top: 10px;
        left: 20px;
    }
    #an2:hover{
        cursor: pointer;
    }
    #an3{
        width: 70px;
        height: 40px;
        position: absolute;
        top: 100px;
        left: 950px;
    }
    #an3:hover{
        cursor: pointer;
    }


    #yyk{				/* 以这个为锚点 */
        position: absolute;
        top: 265px;
        width: 98%;
        height: 500px;
        padding-top: 30px;
        margin-left: 25px;
        /* background-color: #0000FF; */
        background-color: #F6F6F6;
    }
    .yykuai{
        position: relative;
        margin-left: 30px;
        margin-bottom: 10px;
        width: 220px;
        height: 190px;
        background-color: #F6F6F6;
        float: left;
        /* border: 1px solid black; */
    }
    .yytp{
        /* background-color: red; */
        width: 220px;
        /* height: 220px; */
        margin-bottom: 7px;
        border-radius: 7px;
    }
    .yyzz{
        color: gray;
    }
    .yyname:hover,.yyzz:hover{
        color: #90CD98;
    }

    .buofan{
        width: 35px;
        height: 45px;
        background-color: white;
        font-size: 25px;
        border-radius: 50px;
        position: absolute;
        top: 35px;
        left: 80px;
        padding-left: 20px;
        padding-top: 10px;
    }
    .buofan:hover{
        box-shadow: 1px 1px 10px white;
        cursor: pointer;
    }
</style>