<template>
    <div>
    <hands></hands>
    <handtiao2></handtiao2>

        <div id="czgszhuti">

            <img :src=" '/api/tp/bg_singer.jpg' " id="toutp">
            <input type="" name="" id="ss" value="" v-model="namelikes" placeholder="    搜索歌手..."/>
            <img :src=" '/api/tp/QQ截图20210106094850.png'" @click="like" id="ssan">
            <img :src=" '/api/tp/QQ截图20210106104608.png'" @click="$router.push('idnex')" id="an2">
            <input type="button" name="" id="an3" value="返回全部" @click="loadPosts"/>

            <div id="gskuai">

                <div v-for="gs in gesou" :key="gs.pid">
                    <div class="gsbianli">
                        <img :src=" '/api/geshoutp/'+gs.ptp" class="gstp" @click="$router.push('/KanGeshou/' + gs.pid)">
                        <p class="gsname">{{gs.pname}}</p>
                    </div>
                </div>

                <el-pagination
                        background
                        layout="prev, pager, next"
                        @current-change="handlePageChange"
                        :current-page="page.current"
                        :page-size="page.size"
                        :total="page.total" id="tiao"
                        style="margin-top: 450px;margin-left: 550px">
                </el-pagination><!--第几页、每页多少条、总条数-->
            </div>

        </div>
    <yhfankui></yhfankui>
        <fanhuidinbu></fanhuidinbu>
    </div>
</template>

<script>
    import hands from '../components/hands';
    import handtiao2 from '../comfuyon2/handtiao2';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "cazaogeshou",
        components: {
            hands, handtiao2, yhfankui, fanhuidinbu
        },
        data() {
            return {
                page: {
                    current: 1,//第几页
                    size: 10,   //每页的数量
                    total: 0    //数据量
                },
                gesou: [],
                namelikes: ''
            }
        },
        methods: {
            loadPosts() {
                this.$axios({
                    url: "/api/kangs",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.gesou = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szgs",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.gesou = resp.data.data;
                });
            }

        },
        created() {
            this.loadPosts();
        }
    }
</script>

<style scoped>
    #czgszhuti{
        margin-top: 40px;
        width: 100%;
        height: 1000px;
        position: relative;
        /* background-color: red; */
        background-color: #F6F6F6;
    }
    #toutp{
        width: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
    }
    #ss{
        width: 440px;
        height: 45px;
        position: absolute;
        top: 95px;
        left: 440px;
        border-radius: 5px;
        font-size: 17px;
    }
    #ssan{
        width: 40px;
        height: 40px;
        position: absolute;
        top: 100px;
        left: 848px;
    }
    #ssan:hover{
        cursor: pointer;
    }
    #gskuai{
        position: absolute;
        top: 265px;
        width: 100%;
        height: 500px;
        padding-top: 50px;
        /* background-color: #0000FF; */
        background-color: #F6F6F6;
    }
    .gsbianli{
        /* background-color: #008000; */
        background-color: #F6F6F6;
        width: 130px;
        height: 200px;
        float: left;
        margin-left: 112px;
    }
    .gstp{
        width: 126px;
        height: 126px;
        border-radius: 500px;
    }
    .gsname{
        margin-left: 40px;
    }
    #an2{
        width: 40px;
        height: 40px;
        position: absolute;
        top: 10px;
        left: 20px;
    }
    #an2:hover{
        cursor: pointer;
    }
    #an3{
        width: 70px;
        height: 40px;
        position: absolute;
        top: 100px;
        left: 950px;
    }
    #an3:hover{
        cursor: pointer;
    }
</style>