        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/phbone",
                }).then(r => {
                    console.log(r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            loadPosts2() {
                this.$axios({
                    url: "/api/phbtwo",
                }).then(r => {
                    console.log(r);
                    this.yinyue2 = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            loadPosts3() {
