                }).then(r => {
                    console.log(r);
                    this.yinyue4 = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            loadPosts5() {
                this.$axios({
                    url: "/api/phbwu",
                }).then(r => {
                    console.log(r);
                    this.yinyue5 = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message("亲，你还没有登录哦~");
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });

            }

        },
        created() {
            this.loadPosts();
            this.loadPosts2();
            this.loadPosts3();
            this.loadPosts4();
            this.loadPosts5();
            this.doLogin2();
        }
    }
</script>

