<template>

    <div id="yinyuezhuti">

        <h1>新 碟 首 发</h1>

        <div id="xiaozi">
            <span>国语</span>
            <span>粤语</span>
            <span>流行</span>
            <span>轻音乐</span>
            <span>古风</span>
            <span>DJ</span>
        </div>

        <div id="yyk">
            <div v-for="yy in yinyue" :key="yy.mid">
                <div class="yykuai">
                    <div class="yytpdiv">
                        <img :src=" '/api/yytp/' +yy.mtp" class="yytp">
                    </div>
                    <span class="yyname">{{yy.mname.substring(0,yy.mname.indexOf('.'))}}</span><br>
                    <span class="yyzz">{{yy.mauthor}}</span>
<!--                    <div class="buofan"  @click="$router.push('/KanYinyue/' + yy.mid)">▶</div>-->
<!--                    <div class="tsvip" v-if="yy.mvip==1">vip</div>-->
                    <template v-if="yhsf==null">
                            <template v-if="yy.mvip==1">
                                <div class="buofan"  @click="tisi">▶</div>
                                <div class="tsvip" v-if="yy.mvip==1">vip</div>
                            </template>
                            <template v-else>
                                <div class="buofan"  @click="$router.push('/KanYinyue/' + yy.mid)">▶</div>
                                <div class="tsvip" v-if="yy.mvip==1">vip</div>
                            </template>
                    </template>

                    <template v-else>
                        <template v-if="(yy.mvip==1&&yhsfvip==1)||(yy.mvip==0&&yhsfvip==1)||(yy.mvip==0&&yhsfvip==0)">
                            <div class="buofan"  @click="$router.push('/KanYinyue/' + yy.mid)">▶</div>
                            <div class="tsvip" v-if="yy.mvip==1">vip</div>
                        </template>
                        <template v-if="yy.mvip==1&&yhsfvip==0">
                            <div class="buofan"  @click="$router.push('/vip')">▶</div>
                            <div class="tsvip" v-if="yy.mvip==1">vip</div>
                        </template>
                    </template>


                </div>
            </div>

        </div>


    </div>

</template>

<script>
    export default {
        name: "yinyueindex",
        data() {
            return {
                yinyue: [],
                yh: {},
                yhsfvip: {},
                yhsf: '有呀'
            }
        },
        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/zyyinyue",
                }).then(r => {
                    // console.log(r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message('亲，你还没有登录哦~');
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            // console.log(this.yhsfvip);
            this.loadPosts();
            this.doLogin2();
        }
    }
</script>

<style scoped>
    #yinyuezhuti{
        margin-top: 0px;
        background-color: #F6F6F6;
        width: 100%;
        height: 780px;
        position: relative;
    }
    h1{
        /* position: absolute;
        top: 25px;
        left: 550px; */
        margin-left: 580px;
        margin-bottom: 80px;
        font-size: 33px;
        font-weight: 400;
    }
    #yyk{				/* 以这个为锚点 */
        width: 95%;
        /* background-color: #000000; */
        height: 300px;
        margin-left: 50px;
    }
    .yykuai{
        position: relative;
        margin-left: 25px;
        width: 220px;
        height: 300px;
        background-color: #F6F6F6;
        float: left;
        /*box-shadow: 1px 1px 10px gray;*/
    }
    .yytpdiv{
        width: 220px;
        height: 220px;
        margin-bottom: 10px;
        background-color: #F6F6F6;
        overflow: hidden;
        border-radius: 7px;
    }
    .yytp{
        width: 220px;
        height: 220px;
        margin-bottom: 10px;
        border-radius: 7px;
    }
    .yytp:hover{
        width: 240px;
        height: 240px;
        opacity: 0.8;
        transition: width 1s, height 1s;
    }
    .yyzz{
        color: gray;
    }
    .yyname:hover,.yyzz:hover{
        color: #90CD98;
    }

    .tsvip{
        width: 30px;
        background-color: green;
        position: absolute;
        top: 0px;
        left: 0px;
        color: white;
        border-radius: 3px;
        opacity: 0.8;
    }
    .buofan{
        width: 35px;
        height: 45px;
        background-color: white;
        font-size: 25px;
        border-radius: 50px;
        position: absolute;
        top: 80px;
        left: 80px;
        padding-left: 20px;
        padding-top: 10px;
        color: #90CD98;
    }
    .buofan:hover{
        box-shadow: 1px 1px 30px white;
        cursor: pointer;
    }
    #xiaozi{
        position: absolute;
        top: 80px;
        left: 430px;
    }
    #xiaozi span{
        margin-right: 50px;
    }
    #xiaozi span:hover{
        color: #90CD98;
    }
</style>