<template>
    <div>
        <hands></hands>
        <handtiao></handtiao>

        <div id="yyxxzhuti">

            <div id="spkuai">
                <video id="sp" :src=" '/api/shipin/'+yy.vname" controls="controls"></video>
                <h1 id="spname">{{yy.vname.substring(0,yy.vname.indexOf('.'))}}&nbsp;/&nbsp;{{yy.vauthor}}</h1>
                <h1 id="spsc" @click="tjsc">收藏({{likeshus}})</h1>
                <a href="#mypl"><h1 id="sppl" >评论({{page.total}})</h1></a>
            </div>

            <div id="gdsp">
                <h1 id="gdzi">更多他的视频</h1>
                <div v-for="wsp in qwsp" :key="wsp.vid">
                    <div class="yykuai">
                        <video :src=" '/api/shipin/'+wsp.vname" class="yytp"></video>
                        <span class="yyname">{{wsp.vname.substring(0,wsp.vname.indexOf('.'))}}</span><br>
                        <span class="yyzz">{{wsp.vauthor}}</span>
                        <template v-if="yhsf==null">
                            <template v-if="wsp.vvip==1">
                                <div class="buofan"  @click="tisi">▶</div>
                                <div class="tsvip" v-if="wsp.vvip==1">vip</div>
                            </template>
                            <template v-else>
                                <div class="buofan"  @click="$router.push('/KanShipinDiergeo/' + wsp.vid)">▶</div>
                                <div class="tsvip" v-if="wsp.vvip==1">vip</div>
                            </template>
                        </template>

                        <template v-else>
                            <template v-if="(wsp.vvip==1&&yhsfvip==1)||(wsp.vvip==0&&yhsfvip==1)||(wsp.vvip==0&&yhsfvip==0)">
                                <div class="buofan"  @click="$router.push('/KanShipinDiergeo/' + wsp.vid)">▶</div>
                                <div class="tsvip" v-if="wsp.vvip==1">vip</div>
                            </template>
                            <template v-if="wsp.vvip==1&&yhsfvip==0">
                                <div class="buofan"  @click="$router.push('/vip')">▶</div>
                                <div class="tsvip" v-if="wsp.vvip==1">vip</div>
                            </template>
                        </template>
<!--                        <div class="buofan">▶</div>-->
                    </div>
                </div>
            </div>


            <a name="mypl"></a>
            <div id="pl">
                <h1 id="zione">评论</h1>
                <p id="zitwo">共" {{page.total}} "条评论</p>
                <textarea rows="6" cols="130" v-model="pl" placeholder="期待你的神评..." id="srpl"></textarea>
                <input type="submit"  id="an3" value="发表评论"  @click="saveComment"/>
            </div>


            <div id="pllb">
                <span id="jcpl">精彩评论（{{page.total}}条）</span>
                <div>
                    <div v-for="pl in pinlun" :key="pl.vcid">
                        <div class="plnr">
                            <img :src=" '/api/img2/' + pl.yhtx" class="pltx">
                            <span class="plname">{{pl.yhname}}</span>
                            <p class="nr">{{pl.vComment}}</p>
                            <span class="plsj">{{pl.mtime}}</span>
                            <span class="pllike" @click="dzsppl(pl.vcid)">♥({{pl.vclike}})</span>
                        </div>
                    </div>

                </div>
            </div>

                <el-pagination
                        background
                        layout="prev, pager, next"
                        @current-change="handlePageChange"
                        :current-page="page.current"
                        :page-size="page.size"
                        :total="page.total" id="tiao"
                        style="margin-left: 500px">
                </el-pagination><!--第几页、每页多少条、总条数-->

        </div>

        <yhfankui></yhfankui>
        <fanhuidinbu></fanhuidinbu>
    </div>
</template>

<script>
    import hands from '../components/hands';
    import handtiao from '../compone1/handtiao';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "KanShipin",
        components: {
            hands, handtiao, yhfankui, fanhuidinbu
        },
        data() {
            return {
                yyc: {id: null, name: null, tx: null },
                yy: {},
                pl: null,
                yh: {},
                page: {
                    current: 1,//第几页
                    size: 5,   //每页的数量
                    total: 0    //数据量
                },
                pinlun: [],
                yhsfvip: {},
                yhsf: '有呀',
                likeshus: {},
                qwsp: {}
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/yhkansp?vid=" + this.yyc.id
                }).then(r => {
                    console.log(r);
                    this.yy = r.data.data;
                    this.qianwutiao(this.yy.vauthor);
                }).catch(e => {
                    this.$alert(e);
                });
            },
            qianwutiao(name){
                this.$axios({
                    url: "/api/qwsp?vauthor=" + name
                }).then(r => {
                    console.log(r);
                    this.qwsp = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            kanlikeshu(){
                this.$axios({
                    url: "/api/likeshusp?vid=" + this.yyc.id
                }).then(r => {
                    console.log(r);
                    this.likeshus = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            kanpl() {
                this.$axios({
                    url: "/api/sppl?vid=" + this.yyc.id,
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    },
                }).then(r => {
                    console.log(r);
                    this.pinlun = r.data.data;
                    this.page.total = r.data.page.total;
                    console.log(r.data.page.total)
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.kanpl();
            },

            saveComment() {
                if (!this.pl) {
                    // this.$alert("亲，你还没有输入哦~");
                    // this.$message("亲，你还没有输入哦~");
                    this.$message({
                        showClose: true,
                        message: '亲，你还没有输入哦~',
                        type: 'warning'
                    });
                    return;
                }else if(this.yhsf==null){
                    // this.$alert("系统未检测到账户~");
                    // this.$message("系统未检测到账户~");
                    this.$message({
                        showClose: true,
                        message: '系统未检测到账户~',
                        type: 'warning'
                    });
                    return;
                }
                let data = new URLSearchParams();
                data.append("yhid", this.yh.id);
                data.append("yhname", this.yh.name);
                data.append("yhtx", this.yh.tp);
                data.append("pl", this.pl);
                data.append("yyid", this.yyc.id);
                this.$axios({
                    method: "post",
                    url: "/api/addsppl",
                    data: data
                }).then(r => {
                    // this.$message("评论发表成功！");
                    this.$message({
                        showClose: true,
                        message: '评论发表成功！',
                        type: 'success',
                        offset: 200
                    });
                    this.kanpl();
                    this.pl = null;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            tjsc(){
                if(this.yhsf==null){
                    // this.$alert("系统未检测到账户~");
                    // this.$message("系统未检测到账户~");
                    this.$message({
                        showClose: true,
                        message: '系统未检测到账户~',
                        type: 'warning'
                    });
                    return;
                }
                let data = new URLSearchParams();
                data.append("yhid", this.yh.id);
                data.append("yyid", this.yy.vid);
                data.append("yyname", this.yy.vname);
                data.append("yyzz", this.yy.vauthor);
                data.append("yylike", this.yy.vlike);
                data.append("yyvip", this.yy.vvip);
                this.$axios({
                    method: "post",
                    url: "/api/mylikesp",
                    data: data
                }).then(r => {
                    this.kanlikeshu();
                    this.dzsp();
                    // this.$message("已添加的我的喜欢");
                    this.$message({
                        showClose: true,
                        message: '已添加的我的喜欢',
                        type: 'success',
                        offset: 200
                    });
                }).catch(e => {
                    this.$alert(e);
                });
            },
            dzsp(){
                this.$axios({
                    url: "/api/dzsp?vid=" + this.yyc.id
                }).then(r => {
                    console.log(111)
                }).catch(e => {
                    this.$alert(e);
                });
            },
            dzsppl(vcid){
                this.$axios({
                    url: "/api/dzsppl?vcid=" + vcid
                }).then(r => {
                    console.log(111);
                    this.kanpl()
                }).catch(e => {
                    this.$alert(e);
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message("亲，你还没有登录哦~");
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            this.yyc.id = this.$route.params["vid"];
            console.log(this.yyc.id);
            this.doLogin();
            // this.doLogin(this.$route.params["mid"]);
            this.doLogin2();
            this.kanpl();
            window.scrollTo({
                top: 0,
                behavior: "instant"
                // 瞬间置顶
            });
            this.kanlikeshu();
        }
    }
</script>

