<template>
    <div id="zhuyei">

        <hands></hands>
        <handtiao></handtiao>

        <Lunbuo></Lunbuo>
        <yinyueindex></yinyueindex>

                <a name="mypl"></a>
        <paohanban></paohanban>

        <shipinindex></shipinindex>

        <yhfankui></yhfankui>
        <fanhuidinbu></fanhuidinbu>
    </div>
</template>

<script>
    import hands from '../components/hands';
    import handtiao from '../compone1/handtiao';
    import yinyueindex from '../compone1/yinyueindex';
    import Lunbuo from '../compone1/Lunbuo';
    import yhfankui from '../compone1/yhfankui';
    import shipinindex from '../compone1/shipinindex';
    import paohanban from '../compone1/paohanban';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "idnex",
        components: {
            hands, handtiao, yinyueindex, Lunbuo, yhfankui, shipinindex, paohanban, fanhuidinbu
        },
        data() {
            return {
                yh: {},
                wgshu: {},
                wgxx: {},
                zhuhe: {}
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    this.yh = resp.data.data;
                    this.isfo = true
                    if (this.yh!=404){
                        this.weiguishu();
                        // this.weigui();
                    }
                    if(this.yh.viptime1 == 999){
                        this.$alert("您的账号因为多次评论违规，已被封禁10天！");
                        this.zhuxiao()
                    }
                });
            },
            zhuxiao() {
                this.$axios({
                    url: "/api/yhzx",
                }).then(resp =>{
                    this.$router.push('/yhden')
                });
            },
            weiguishu() {
                this.$axios({
                    url: "/api/jgshu?id=" + this.yh.id,
                }).then(resp =>{
                    console.log(resp);
                    this.wgshu = resp.data.data;
                    if (this.wgshu<3){
                        this.weigui();
                    }
                    if (this.wgshu>=3){
                        this.fenhao();
                        this.fenhaoscpl();
                        this.$alert("您的账号因为多次评论违规，已被封禁10天！");
                        this.zhuxiao()
                    }
                });
            },
            weigui() {
                this.$axios({
                    url: "/api/jgyh?id=" + this.yh.id,
                }).then(resp =>{
                    console.log(resp);
                    this.wgxx = resp.data.data;
                    if (this.wgxx != null){
                        console.log(this.wgxx, 222);
                        this.zhuhe = "尊敬的用户，你因在"+this.wgxx.mtime+"评论“"+this.wgxx.mComment+"”被系统警告"+this.wgshu+"次！" +
                            "达到3次将被系统封号10天！！！"
                        this.$alert(this.zhuhe);
                        this.weiguibian()
                    }
                });
            },
            weiguibian() {
                this.$axios({
                    url: "/api/jgbian?id=" + this.wgxx.jgid,
                }).then(resp =>{
                    console.log(resp);
                    this.wgshu = resp.data.data;
                    this.weigui()
                });
            },
            fenhao() {
                this.$axios({
                    url: "/api/fenhao?id=" + this.yh.id,
                }).then(resp =>{
                    console.log(resp);
                });
            },
            fenhaoscpl(){
                this.$axios({
                    url: "/api/fhscpl?id=" + this.yh.id,
                }).then(resp =>{
                    console.log(resp);
                });
            }

        },
        created() {
            this.doLogin();
        }
    }
</script>

