<template>
    <div>

        <div id="dl">
            <p id="h">维尔音乐用户注册</p>
            <form :model="form">
                <img  id="tu3"  title="请选择头像" @click="$refs.x.click()" :src="previewSrc">
                <input style="display: none;" id="wj2" type="file" accept=".jpg, .gif" ref="x" @change="filePick">
                <input type="text" name="name" id="in" placeholder="  请输入用户名" v-model="form.name" value="" /><br>
                <input type="password" name="pwd" id="in2"  placeholder="  请输入密码" v-model="form.pwd" value="" /><br>
            </form>
            <input type="submit" @click="save" id="an" value="注册"  />

            <a href="#" id="yi">意见反馈</a>
        </div>


        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%">
            <span>注册成功！</span>
            <span slot="footer" class="dialog-footer">
        <!--            <el-button @click="dialogVisible = false">取 消</el-button>-->
                    <el-button type="primary" @click="$router.push('/yhden')" >立即登录</el-button>
                </span>
        </el-dialog>

    </div>
</template>

<script>

    export default {
        name: "yhdendiv2",
        data() {
            return {
                form: {
                    name: '',
                    pwd: '',
                    cover: null,
                },
                previewSrc: null,
                dialogVisible: false
            }
        },
        methods: {
            save() {
                var formData = new FormData();
                formData.append("name", this.form.name);
                formData.append("pwd", this.form.pwd);
                formData.append("cover", this.form.cover);

                console.log(this.form.cover)

                this.$axios({
                    url: '/api/yhzc',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("注册成功！");
                    // this.do()
                    this.$router.push('/yhden')
                }).catch(e => {
                    alert(222)
                });
            },
            filePick() {
                this.previewSrc = URL.createObjectURL(this.$refs.x.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.cover = this.$refs.x.files[0];
            },
            do(){
                this.dialogVisible= true
            }
        },
        // created() {
        //     this.doLogin();
        // }
    }

</script>

<style scoped>
    #dl{
        position: absolute;
        right: 200px;
        top: 120px;
        /*border: 1px solid black;*/
        width: 390px;
        height: 320px;
        border-radius: 10px;
        background-color: white;
        box-shadow: 1px 1px 2px white;
    }
    #h{
        font-size: 20px;
        margin-left: 110px;
        margin-top: 10px;
    }
    a{
        color: #6f95c8;
        text-decoration:none;
    }
    a:hover{
        text-decoration: underline;
    }
    #in{
        margin-top: 70px;
        width: 240px;
        height: 35px;
        margin-left: 70px;
        border-radius: 5px;
        /*border: 1px solid #2F4056;*/
        border: 1px solid skyblue;
    }
    #in2{
        width: 240px;
        height: 35px;
        margin-top: 15px;
        margin-left: 70px;
        border-radius: 5px;
        border: 0px;
        /*border: 1px solid #2F4056;*/
        border: 1px solid skyblue;
    }
    #in:hover,#in2:hover{
        border: 1px solid #007DDB;
    }
    #an{
        width: 248px;
        height: 38px;
        margin-top: 15px;
        margin-left: 70px;
        font-size: 20px;
        background-color: #55aaff;
        border: 0px;
        border-radius: 5px;
    }
    #xin{
        position: absolute;
        bottom: 10px;
        right: 100px;
    }
    #yi{
        position: absolute;
        bottom: 10px;
        right: 20px;
    }
    #tu3{
        position: absolute;
        top: 50px;
        left: 160px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #2F4056;
        color: white;
    }
</style>