<template>
    <div>

    <hands></hands>
    <handtiao></handtiao>

    <div id="yyxxzhuti">

        <div id="buofan">
            <img :src=" '/api/yytp/'+ yy.mtp " id="yytu">
            <h1 id="yyxxname">{{yy.mname.substring(0,yy.mname.indexOf('.'))}}</h1>
            <p id="yyxxzz">{{yy.mauthor}}</p>
            <p id="yyxxlx">流派：{{yy.mlx}}</p>
            <p class="yyxxvip" v-if="yy.mvip!=1">类型：免费音乐</p>
            <p class="yyxxvip" v-else>类型：会员专享</p>
<!--            <audio :src=" '/api/yinyue/'+yy.mname" controls="controls" id="yybf"  @click="bfk"></audio>-->
            <div id="bfan" @click="bfk">▶播放</div>
            <div id="an" @click="tjsc">收藏({{likeshus}})</div>
<!--            <input type="submit"  id="an" value="收藏(90)"  @click="tjsc"/>-->
            <a href="#mypl"><div id="an2">评论({{page.total}})</div></a>
        </div>

        <a name="mypl"></a>
        <div id="pl">
            <h1 id="zione">评论</h1>
            <p id="zitwo">共" {{page.total}} "条评论</p>
            <textarea rows="6" cols="130" v-model="pl" placeholder="期待你的神评..." id="srpl"></textarea>
            <input type="submit"  id="an3" value="发表评论"  @click="saveComment"/>
        </div>


        <div id="pllb">
            <span id="jcpl">精彩评论（{{page.total}}条）</span>
            <div>
                <div v-for="pl in pinlun" :key="pl.mcid">
                    <div class="plnr">
                        <img :src=" '/api/img2/' + pl.yhtx" class="pltx">
                        <span class="plname">{{pl.yhname}}</span>
                        <p class="nr">{{pl.mComment}}</p>
                        <span class="plsj">{{pl.mtime}}</span>
                        <span class="pllike" @click="dzyypl(pl.mcid)">♥({{pl.mclike}})</span>
                    </div>
                </div>

            </div>
        </div>


        <el-pagination
                background
                layout="prev, pager, next"
                @current-change="handlePageChange"
                :current-page="page.current"
                :page-size="page.size"
                :total="page.total" id="tiao"
                style="margin-left: 500px">
        </el-pagination><!--第几页、每页多少条、总条数-->


    </div>
        <yhfankui></yhfankui>
        <fanhuidinbu></fanhuidinbu>

        <template v-if="bfl">
            <div id="yyfdkuai">
                <img :src=" '/api/yytp/'+yy.mtp" id="yyfdtu">
                <span id="yyfdname">{{yy.mname.substring(0,yy.mname.indexOf('.'))}}</span>
                <span id="yyfdzz">{{yy.mauthor}}</span>
                <audio :src=" '/api/yinyue/'+yy.mname" controls="controls" autoplay="autoplay" id="yyfdbf"></audio>
                <span id="yyfdguanbi"  @click="bfgb">X</span>
                <template v-if="yy.mvip==1">
                    <img :src=" '/api/tp/vip.png' " id="yyfdhuiyuan" title="唯尔vip专属~" @click="$router.push('/vip')">
                </template>
            </div>
        </template>

    </div>
</template>

<script>
    import hands from '../components/hands';
    import handtiao from '../compone1/handtiao';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "KanYinyue",
        components: {
            hands, handtiao, yhfankui, fanhuidinbu
        },
        data() {
            return {
                yyc: {id: null, name: null, tx: null },
                yy: {},
                pl: null,
                yh: {},
                page: {
                    current: 1,//第几页
                    size: 5,   //每页的数量
                    total: 0    //数据量
                },
                pinlun: [],
                yhsf: '有呀',
                likeshus: {},
                bfl: false
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/yhkanyy?mid=" + this.yyc.id
                }).then(r => {
                    console.log(r);
                    this.yy = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            kanlikeshu(){
                this.$axios({
                    url: "/api/likeshu?mid=" + this.yyc.id
                }).then(r => {
                    console.log(r);
                    this.likeshus = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            kanpl() {
                this.$axios({
                    url: "/api/yypl?mid=" + this.yyc.id,
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    },
                }).then(r => {
                    console.log(r);
                    this.pinlun = r.data.data;
                    this.page.total = r.data.page.total;
                    console.log(r.data.page.total)
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.kanpl();
            },

            saveComment() {
                if (!this.pl) {
                    // this.$alert("亲，你还没有输入哦~");
                    // this.$message('亲，你还没有输入哦~');
                    this.$message({
                        showClose: true,
                        message: '亲，你还没有输入哦~',
                        type: 'warning'
                    });
                    return;
                }else if(this.yhsf==null){
                    // this.$alert("系统未检测到账户~");
                    // this.$message('系统未检测到账户~');
                    this.$message({
                        showClose: true,
                        message: '系统未检测到账户~',
                        type: 'warning'
                    });
                    return;
                }
                let data = new URLSearchParams();
                data.append("yhid", this.yh.id);
                data.append("yhname", this.yh.name);
                data.append("yhtx", this.yh.tp);
                data.append("pl", this.pl);
                data.append("yyid", this.yyc.id);
                this.$axios({
                    method: "post",
                    url: "/api/addyypl",
                    data: data
                }).then(r => {
                    // this.$message("评论发表成功！");
                    this.$message({
                        showClose: true,
                        message: '评论发表成功！',
                        type: 'success',
                        offset: 200
                    });
                    this.kanpl();
                    this.pl = null;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            tjsc(){
                if(this.yhsf==null){
                    // this.$alert("系统未检测到账户~");
                    // this.$message('系统未检测到账户~');
                    this.$message({
                        showClose: true,
                        message: '系统未检测到账户~',
                        type: 'warning'
                    });
                    return;
                }
                let data = new URLSearchParams();
                data.append("yhid", this.yh.id);
                data.append("yyid", this.yy.mid);
                data.append("yyname", this.yy.mname);
                data.append("yyzz", this.yy.mauthor);
                data.append("yytp", this.yy.mtp);
                data.append("yylike", this.yy.mlike);
                data.append("yyvip", this.yy.mvip);
                data.append("yylx", this.yy.mlx);
                this.$axios({
                    method: "post",
                    url: "/api/mylikeyy",
                    data: data
                }).then(r => {
                    this.kanlikeshu();
                    this.dzyy();
                    // this.$message("已添加的我的喜欢");
                    this.$message({
                        showClose: true,
                        message: '已添加的我的喜欢',
                        type: 'success',
                        offset: 200
                    });
                }).catch(e => {
                    this.$alert(e);
                });
            },
            dzyy(){
                this.$axios({
                    url: "/api/dzyy?mid=" + this.yyc.id
                }).then(r => {
                    console.log(111)
                }).catch(e => {
                    this.$alert(e);
                });
            },
            dzyypl(mcid){
                this.$axios({
                    url: "/api/dzyypl?mcid=" + mcid
                }).then(r => {
                    console.log(111);
                    this.kanpl()
                }).catch(e => {
                    this.$alert(e);
                });
            },
            bfk(){
                this.bfl = true
            },
            bfgb(){
                this.bfl = false
            }

        },
        created() {
            this.yyc.id = this.$route.params["mid"];
            console.log(this.yyc.id);
            this.doLogin();
            // this.doLogin(this.$route.params["mid"]);
            this.doLogin2();
            this.kanpl();
            window.scrollTo({
                top: 0,
                behavior: "instant"
                                    // 瞬间置顶
            });
            this.kanlikeshu();
        }
    }
</script>

