<template>
    <div>
        <hands2></hands2>

        <div id="bjzhuti">
            <template v-if="bjt==null">
                <img :src=" '/api/tp/bg_profile.jpg' " class="bjt">
            </template>
            <template v-else>
                <img :src=" '/api/vipbjt/'+bjt " class="bjt">
            </template>
            <!--            <img :src=" '/api/vipbjt/'+bjt " class="bjt">-->
            <img :src=" '/api/img2/'+yh.tp " id="tx">
            <h1 id="name">{{yh.name}}</h1>
            <template v-if="yhsfvip==1">
                <img :src=" '/api/tp/vip.png' " class="huiy" title="唯尔vip专属~" @click="$router.push('/vip')">
            </template>
            <template v-else>
                <img :src=" '/api/tp/bsvip.png' " class="huiy" title="你还没有点亮vip哦~" @click="$router.push('/vip')">
            </template>
            <!--            <h1 id="sc1">{{likeshus2}}</h1>-->
            <h1 id="sc1">{{likeshus}}</h1>
            <h1 id="sc2">收藏</h1>
            <h1 id="gz1">{{likeshus4}}</h1>
            <h1 id="gz2">关注</h1>
            <h1 id="mylike" @click="$router.push('/YonHu')">我的喜欢</h1>
            <h1 id="mygz" @click="$router.push('/YonHu4')">我的关注</h1>
            <h1 id="mypl" @click="$router.push('/YonHu5')">我的评论</h1>
            <h1 id="myhuifu" @click="$router.push('/YonHu3fh')">反馈信息({{likeshus3}})</h1>
            <template v-if="yhsfvip==1">
                <h1 class="huanfu" @click="bjtxuanz">☺个性化</h1>
            </template>
            <template v-else>
                <h1 class="huanfu" @click="tisi">☺个性化</h1>
            </template>

            <template v-if="beijt">
                <form :model="form">
                    <div id="bjtxz">
                        <img  id="bjtt"  title="选择你的个性背景图..." @click="$refs.x.click()" :src="previewSrc">
                        <input style="display: none;" id="wj2" type="file" accept=".jpg, .gif" ref="x" @change="filePick">
                        <input type="button" id="bjtok" value="确定" @click="savePost(yh.id)"/>
                        <input type="button" id="bjtno" value="取消" @click="bjtxuanz2"/>
                    </div>
                </form>
            </template>

        </div>

        <YonHuhuifus></YonHuhuifus>
        <yhfankui></yhfankui>
        <fanhuidinbu></fanhuidinbu>
    </div>

</template>

<script>
    import hands2 from '../comfuyon2/hands2';
    import YonHuhuifus from '../compone1/YonHuhuifus';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "YonHu",
        components: {
            hands2, YonHuhuifus, yhfankui, fanhuidinbu
        },
        data() {
            return {
                yh: {},
                likeshus: {},
                likeshus2: {},
                likeshus3: {},
                likeshus4: {},
                yhid: 0,
                beijt: false,
                form: {
                    cover: null,
                },
                previewSrc: null,
                bjt: {},
                yhsfvip: {},
                yhsf: '有呀'
            }
        },
        methods: {
            savePost(id) {
                var formData = new FormData();
                formData.append("yhid", id);
                formData.append("cover", this.form.cover);

                console.log(this.form.cover)

                this.$axios({
                    url: '/api/vipbjt',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // this.$message("上传成功!");
                    this.$message({
                        showClose: true,
                        message: '上传成功!请刷新页面',
                        type: 'success',
                        offset: 200
                    });
                    this.beijt = false
                }).catch(e => {
                    alert(222)
                });
            },
            filePick() {
                this.previewSrc = URL.createObjectURL(this.$refs.x.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.cover = this.$refs.x.files[0];
            },
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.yhid = resp.data.data.id;
                        this.czvip();
                        this.likeshu();
                        this.likeshu2();
                        this.likeshu3();
                        this.likeshu4();
                        this.vipkanbjt();
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            likeshu(){
                this.$axios({
                    url: "/api/yhlikeyyshu?id=" + this.yhid
                }).then(r => {
                    console.log(r);
                    this.likeshus = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu2(){
                this.$axios({
                    url: "/api/yhlikespshu?id=" + this.yhid
                }).then(r => {
                    console.log(r);
                    this.likeshus2 = r.data.data + this.likeshus;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu3(){
                this.$axios({
                    url: "/api/kanhfshu?id=" + this.yh.id
                }).then(r => {
                    console.log(r);
                    this.likeshus3 = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu4(){
                this.$axios({
                    url: "/api/yhlikegsshu?id=" + this.yh.id
                }).then(r => {
                    console.log(r);
                    this.likeshus4 = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            vipkanbjt(){
                this.$axios({
                    url: "/api/kanbjt?id=" + this.yhid
                }).then(r => {
                    console.log(r);
                    this.bjt = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            bjtxuanz(){
                this.beijt = true
            },
            bjtxuanz2(){
                this.beijt = false
            },
            tisi(){
                // this.$message("亲，你还不是唯尔vip用户哟 ~");
                this.$message({
                    showClose: true,
                    message: '亲，你还不是唯尔vip用户哟 ~',
                    type: 'warning'
                });
            }


        },
        created() {
            this.doLogin2();
        }
    }
</script>

