    <div id="hand">

        <img :src=" '/api/tp/logos.jpg' " id="logo" >
        <h1 id="w">唯尔</h1>
        <div id="one" @click="$router.push('/idnex')">
            音乐馆
        </div>
        <template v-if="isfo">
            <div class="two" @click="$router.push('/YonHu')">
                我的音乐
            </div>
        </template>
        <template v-else>
            <div class="two" @click="tisi">
                我的音乐
            </div>
        </template>

        <template v-if="isfo">
            <div class="three" @click="$router.push('/vip')">
                VIP
            </div>
        </template>
        <template v-else>
            <div class="three" @click="tisi">
                VIP
            </div>
        </template>

        <input type="text" id="in2"  placeholder="  搜索音乐(名称、歌手、类型)"  v-model="likeyy" /><br>
        <input type="submit" id="an" value="搜索" @click="$router.push('/cazaoyinyue/' + likeyy)" />

        <template v-if="isfo">
            <img :src=" '/api/img2/'+ yh.tp " id="yhtx" @click="zhuxiao" title="注销">
            <p id="yhname">{{yh.name}}</p>
            <span id="hy2">{{myzi}}</span>
            <input type="submit" id="an2" value="充值 ▼" @click="$router.push('/vip')"/>
        </template>

        <template v-else>
            <input type="submit" id="an3" value="登录账号" @click="$router.push('/yhden')" />
            <input type="submit" id="an4" value="充值 ▼" @click="tisi"/>
        </template>




        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%">
            <span>已注销！</span>
            <span slot="footer" class="dialog-footer">
                        <!--            <el-button @click="dialogVisible = false">取 消</el-button>-->
                                    <el-button type="primary" @click="$router.push('/yhden')" >确定</el-button>
                                </span>
        </el-dialog>

    </div>

</template>

<script>
    export default {
        name: "hands2",
        data() {
            return {
                yh: {},
                isfo: false,
                dialogVisible: false,
                one: {},
                two: {},
                three: {},
                four: {},
                myzi: {},
                likeyy: null
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        // alert('哈哈哈哈哈哈');
                        this.yh = resp.data.data;
                        this.isfo = true;

                        // this.one = resp.data.data.vip;
                        // this.two = resp.data.data.viptime1;
                        // this.three = resp.data.data.viptime2;
                        // this.four = resp.data.data.viptime3;
                        // this.erzi();
                        this.czvip();
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.one = r.data.data.vip;
                    this.two = r.data.data.viptime1;
                    this.three = r.data.data.viptime2;
                    this.four = r.data.data.viptime3;

                    this.erzi();
                }).catch(e => {
                    alert(222000)
                });
            },
            zhuxiao() {
                this.$axios({
                    url: "/api/yhzx",
                }).then(resp =>{
                    this.dialogVisible= true
                });
            },
            erzi(){
                // console.log(111);
                var my = this.one;//是否会员
                var a = this.two;//月
                var b = this.three;//日
                var c = this.four; //充几个月

                console.log(this)

                if(my==0){
                    // alert(111)
                    this.myzi = "您还不是唯尔vip哟~";
                }else{
                    // alert(2222)
                    var sj = c*30; //会员天数
                    var ti = new Date();
                    var t1 = ti.getMonth() +1;
                    var t2 = ti.getDate();
                    var s;
                    var my2;

                    var one = t1 - a;
                    if(one == -11){   					//一月转换
                        one = 1;
                    }
                    else if(one == 0){       			 	//本月，无错
                        s = t2-b;      //过去的时间
                        my2 = sj-s;		//还剩
                        this.myzi = '会员还剩“ '+my2+' ”天';
                    }
                    else if( sj>(one*30 + (t2-b)) ){			//充值多月，无错
                        if(t2>b){
                            s = one*30 + (t2-b);
                        }else if(t2<b){
                            s = one*30 - (b-t2);
                        }
                        my2 = sj-s;
                        this.myzi = '会员还剩“ '+my2+' ”天';
                    }
                    else if( sj>(one*30 - (b-t2)) ){
                        if(t2>b){
                            s = one*30 + (t2-b);
                        }else if(t2<b){
                            s = one*30 - (b-t2);
                        }
                        my2 = sj-s;
                        this.myzi = '会员还剩“ '+my2+' ”天';
                    }
                    else{
                        this.myzi = '亲，您的会员已过期';								//过期
                    }
                }
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message("亲，你还没有登录哦~");
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            this.doLogin();
            // this.erzi();
        }
    }
</script>

