<template>

    <div id="gskuai">

        <div v-for="gs in yinyue" :key="gs.xhid">
            <div class="gsbianli">
                <img :src=" '/api/geshoutp/'+gs.ptp" class="gstp" @click="$router.push('/KanGeshou/' + gs.pid)">
                <span class="gsname">{{gs.pname}}</span>
                <button type="button" class="scan" @click="deleteComment(gs.pid,yh.id)">已关注</button>
            </div>

        </div>

    </div>

</template>

<script>
    export default {
        name: "YonHuguanzges",
        data() {
            return {
                yinyue: [],
                yh: {},
            }
        },
        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    // console.log(resp);
                    // console.log(resp.data.data);
                    this.yh = resp.data.data;
                    this.bianliyy();
                });
            },
            bianliyy() {
                console.log(this.yh.id);
                this.$axios({
                    url: "/api/yhlkgs?id=" + this.yh.id
                }).then(r => {
                    console.log('好好好好');
                    console.log(r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            deleteComment(id,yhid) {
                this.$confirm("确定要取消喜欢吗?")
                    .then(() => {
                        this.$axios({
                            url: '/api/yhsclkgs?pid=' + id+'&id='+ yhid
                        }).then(r => {
                            this.$message("移除成功!");
                            this.bianliyy();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },

        },
        created() {
            this.doLogin2();
        }
    }
</script>

<style scoped>

    #gskuai{
        position: absolute;
        top: 410px;
        width: 80%;
        height: 500px;
        padding-top: 50px;
        padding-left: 80px;
         /*background-color: #0000FF;*/
        background-color: #F6F6F6;
    }
    .gsbianli{
        /*background-color: #008000;*/
        background-color: #F6F6F6;
        width: 130px;
        height: 200px;
        float: left;
        margin-left: 82px;
        position: relative;
        margin-top: 10px;
    }
    .gstp{
        width: 126px;
        height: 126px;
        border-radius: 500px;
    }
    .gsname{
        margin-left: 40px;
    }
    .scan{
        border: 1px solid gray;
        position: absolute;
        right: 37px;
        top: 155px;
        width: 60px;
        height: 25px;
        color: #90CD98;
        font-weight: 900;
        border-radius: 30px;
    }
    .scan:hover{
        /* background-color: #90CD98; */
        color: orangered;
        cursor: pointer;
    }
</style>