<template>
    <div>
        <div id="dl">
            <p id="h">帐号密码登录</p>
            <template v-if="isfo">
                <img  id="tu3" :src=" '/api/img2/'+ tx ">
            </template>
            <input type="text" name="name" id="in" placeholder="  请输入用户名" v-model="names" value="" /><br>
            <input type="password" name="pwd" id="in2" @click="dentx" placeholder="  请输入密码" v-model="pwd" value="" /><br>
            <input type="submit" @click="den" id="an" value="登录"  />
            <br>
            <a href="#" @click.prevent="$router.push('/yhden2')" id="xin" >注册新账号   |</a>
            <a href="#" id="yi">意见反馈</a>
        </div>


        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%">
            <span>登录成功！</span>
            <span slot="footer" class="dialog-footer">
        <!--            <el-button @click="dialogVisible = false">取 消</el-button>-->
                    <el-button type="primary" @click="$router.push('/idnex')" >进入系统</el-button>
                </span>
        </el-dialog>

    </div>
</template>

<script>

    export default {
        name: "yhdendiv",
        data() {
            return {
                names: '',
                pwd: '',
                tx: '',
                dialogVisible: false,
                isfo: false,
            }
        },
        methods: {
            den() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.names);
                rsp.append("pwd", this.pwd);
                this.$axios({
                    url: "/api/yhdl",
                    method: "post",
                    data: rsp,
                    withCredentials: true
                }).then(resp =>{
                    // console.log(resp);
                    // console.log(resp.data.data)
                    if (resp.data.data == 0){
                        alert("用户名或密码错误！")
                    }else{
                        this.$router.push('/idnex')
                        // this.do()
                    }
                });
            },
            dentx() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.names);
                this.$axios({
                    url: "/api/yhdltx",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data == null){
                        // console.log('没有该用户');
                        this.isfo = false;
                        this.tx = null;
                        this.$message.error('没有该用户哦，亲');
                    }else{
                        // console.log('显示头像');
                        this.isfo = true;
                        this.tx = resp.data.data;
                    }
                });
            },
            // do(){
            //     this.dialogVisible= true
            // }
        },
        // created() {
        //     this.doLogin();
        // }
    }

</script>

<style scoped>
    #dl{
        position: absolute;
        right: 200px;
        top: 120px;
        /*border: 1px solid black;*/
        width: 390px;
        height: 280px;
        border-radius: 10px;
        background-color: white;
        box-shadow: 1px 1px 2px white;
    }
    a{
        color: #6f95c8;
        text-decoration:none;
    }
    a:hover{
        text-decoration: underline;
    }
    #h{
        font-size: 20px;
        margin-left: 130px;
        margin-top: 10px;
    }
    #in{
        margin-top: 15px;
        width: 240px;
        height: 35px;
        margin-left: 70px;
        border-radius: 5px;
        /*border: 1px solid #2F4056;*/
        border: 1px solid skyblue;
    }
    #in2{
        width: 240px;
        height: 35px;
        margin-top: 15px;
        margin-left: 70px;
        border-radius: 5px;
        border: 0px;
        /*border: 1px solid #2F4056;*/
        border: 1px solid skyblue;
    }
    #in:hover,#in2:hover{
        border: 1px solid #007DDB;
    }
    #an{
        width: 248px;
        height: 38px;
        margin-top: 15px;
        margin-left: 68px;
        font-size: 20px;
        background-color: #55aaff;
        border: 0px;
        border-radius: 5px;
    }
    #xin{
        position: absolute;
        bottom: 10px;
        right: 100px;
    }
    #yi{
        position: absolute;
        bottom: 10px;
        right: 20px;
    }
    #tu3{
        position: absolute;
        left: 10px;
        top: 65px;
        width: 50px;
        height: 50px;
        border-radius: 50px;
        /* border: 1px solid gray; */
        /* background-color: #2F4056; */
        /* color: white; */
    }
</style>