                this.t2 = ti.getDate();

                var formData = new FormData();
                formData.append("name", this.yh.name);
                formData.append("vip", this.yue);
                formData.append("y", this.t1);
                formData.append("r", this.t2);

                console.log(this.t1);
                console.log(this.t2);
                console.log(this.yue);
                console.log(this.yh.name);

                this.$axios({
                    url: '/api/vipcz',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("成功！");
                    this.$message({
                        message: '支付成功！',
                        type: 'success',
                        offset: '220'
                    });
                }).catch(e => {
                    alert(222)
                });
            },
            one(){
