<template>
    <div>

        <template v-if="sfdl">
            <div id="fankuizhuti">
                <p id="fkzi" @click="kai">反馈</p>
            </div>
        </template>

                    <el-dialog title="反馈信息" :visible.sync="dialogFormVisible">
                        <el-form :model="form">
                            <el-form-item label="反馈">
                                <el-input type="textarea" v-model="form.fk" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false">取 消</el-button>
                            <el-button type="primary" @click.prevent="huifu" >确 定</el-button>
                        </div>
                    </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "yhfankui",
        data() {
            return {
                sfdl: false,
                dialogFormVisible: false,
                form: {
                    fk: '',
                    yh: {}
                },
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.sfdl = true;
                    }else {
                        this.sfdl = false;
                    }

                });
            },

            huifu() {
                var formData = new FormData();
                formData.append("id", this.yh.id);
                formData.append("name", this.yh.name);
                formData.append("tp", this.yh.tp);
                formData.append("fk", this.form.fk);

                console.log(this.yh.name);

                this.$axios({
                    url: '/api/yhtjfk',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.do();
                    this.$alert("谢谢参与用户体验信息反馈，唯尔音乐平台会认真参考每个用户的意见！");
                    this.form.fk = null
                }).catch(e => {
                    alert(222)
                });
            },
            kai(){
                this.dialogFormVisible = true;
            },
            do(){
                this.dialogFormVisible = false;
            }

        },
        created() {
            this.doLogin();
        }
    }
</script>

<style scoped>
    #fankuizhuti{
        background-color: white;
        width: 35px;
        height: 50px;
        border: 1px solid #6f95c8;
        /* background-color: #90CD98; */
        position: fixed;
        right: 5px;
        bottom: 105px;
        box-shadow: 1px 1px 10px gray;
        opacity: 0.6;
        /* border-top-left-radius: 10px ;
        border-bottom-left-radius: 10px;
        padding-left: 0px; */
    }
    #fankuizhuti:hover{
        opacity: 1;
        box-shadow: 1px 1px 10px #90CD98;
        border: 1px solid #90CD98;
        cursor: pointer;
        width: 40px;
        padding-left: 5px;
        transition: width 0.5s, padding-left 0.5s;
    }
    #fkzi{
        color: black;
        /* border: 1px solid #000000; */
    }
    #fkzi:hover{
        /* width: 40px;
        border-bottom: 1px solid white; */
        color: #90CD98;
    }
</style>