<script>
    import hands from '../components/hands';
    import handtiao4 from '../comfuyon2/handtiao4';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "cazaoshipin",
        components: {
            hands, handtiao4, yhfankui, fanhuidinbu
        },
        data() {
            return {
                page: {
                    current: 1,//第几页
                    size: 10,   //每页的数量
                    total: 0    //数据量
                },
                gesou: [],
                namelikes: '',
                yh: {},
                yhsfvip: {},
                yhsf: '有呀'
            }
        },
        methods: {
            loadPosts() {
                this.$axios({
                    url: "/api/kanyy",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.gesou = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szyy",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.gesou = resp.data.data;
                });
            },
            toubulx(name) {
                var rsp = new URLSearchParams();
                rsp.append("name", name);
                this.$axios({
                    url: "/api/szyy",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.gesou = resp.data.data;
                });
            },

            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message('亲，你还没有登录哦~');
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            this.doLogin2();

            if (this.$route.params["yyname"] == null){
                this.loadPosts();
            }else {
                this.namelikes = this.$route.params["yyname"];
                this.like();
            }
        }
    }
</script>

