<template>
    <div id="yinyuezhuti">

        <h1>高 清 M V</h1>

        <div id="xiaozi">
            <span>国语</span>
            <span>粤语</span>
            <span>流行</span>
            <span>轻音乐</span>
            <span>古风</span>
            <span>DJ</span>
        </div>

        <div id="yyk">
            <div v-for="sp in shipin" :key="sp.vid">
                <div class="yykuai">
                    <video :src=" '/api/shipin/' +sp.vname" class="yytp"></video>
                    <span class="yyname">{{sp.vname.substring(0,sp.vname.indexOf('.'))}}</span><br>
                    <span class="yyzz">{{sp.vauthor}}</span>

                    <template v-if="yhsf==null">
                        <template v-if="sp.vvip==1">
                            <div class="buofan"  @click="tisi">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                        <template v-else>
                            <div class="buofan"  @click="$router.push('/KanShipin/' + sp.vid)">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                    </template>

                    <template v-else>
                        <template v-if="(sp.vvip==1&&yhsfvip==1)||(sp.vvip==0&&yhsfvip==1)||(sp.vvip==0&&yhsfvip==0)">
                            <div class="buofan"  @click="$router.push('/KanShipin/' + sp.vid)">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                        <template v-if="sp.vvip==1&&yhsfvip==0">
                            <div class="buofan"  @click="$router.push('/vip')">▶</div>
                            <div class="tsvip" v-if="sp.vvip==1">vip</div>
                        </template>
                    </template>


                </div>
            </div>

        </div>



    </div>
</template>

<script>
    export default {
        name: "shipinindex",
        data() {
            return {
                shipin: [],
                yh: {},
                yhsfvip: {},
                yhsf: '有呀'
            }
        },
        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/zyshipin",
                }).then(r => {
                    // console.log(r);
                    this.shipin = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                // this.$message('亲，你还没有登录哦~');
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            }

        },
        created() {
            // console.log(this.yhsfvip);
            this.loadPosts();
            this.doLogin2();
        }
    }
</script>

<style scoped>
    .tsvip{
        width: 30px;
        background-color: green;
        position: absolute;
        top: 0px;
        left: 0px;
        color: white;
        border-radius: 3px;
        opacity: 0.8;
    }
    #yinyuezhuti{
        background-color: #F6F6F6;
        width: 100%;
        height: 600px;
        position: relative;
    }
    h1{
        /* position: absolute;
        top: 25px;
        left: 550px; */
        margin-left: 580px;
        margin-bottom: 80px;
        font-size: 33px;
        font-weight: 400;
    }
    #yyk{				/* 以这个为锚点 */
        width: 95%;
        /* background-color: #000000; */
        height: 300px;
        margin-left: 50px;
    }
    .yykuai{
        position: relative;
        margin-left: 20px;
        margin-bottom: 10px;
        width: 220px;
        height: 190px;
        background-color: #F6F6F6;
        float: left;
        /* border: 1px solid black; */
    }
    .yytp{
        /* background-color: red; */
        width: 220px;
        /* height: 220px; */
        margin-bottom: 7px;
        border-radius: 7px;
    }
    .yyzz{
        color: gray;
    }
    .yyname:hover,.yyzz:hover{
        color: #90CD98;
    }

    .buofan{
        width: 35px;
        height: 45px;
        background-color: white;
        font-size: 25px;
        border-radius: 50px;
        position: absolute;
        top: 35px;
        left: 80px;
        padding-left: 20px;
        padding-top: 10px;
    }
    .buofan:hover{
        box-shadow: 1px 1px 10px white;
        cursor: pointer;
    }
    #xiaozi{
        position: absolute;
        top: 80px;
        left: 430px;
    }
    #xiaozi span{
        margin-right: 50px;
    }
    #xiaozi span:hover{
        color: #90CD98;
    }
</style>