<template>
    <div id="xhgequ">
        <h1 id="ge" @click="$router.push('/YonHu')">歌曲 {{likeshus2}}</h1>
        <h1 id="sp">视频 {{likeshus}}</h1>
        <div id="xian"></div>


        <div id="yyk">
            <div v-for="sp in yinyue" :key="sp.xhid">
                <div class="yykuai">
                    <video :src=" '/api/shipin/'+sp.vname" class="yytp"></video>
                    <span class="yyname">{{sp.vname.substring(0,sp.vname.indexOf('.'))}}</span><br>
                    <span class="yyzz">{{sp.vauthor}}</span>
                    <div class="buofan" @click="$router.push('/KanShipin/' + sp.vid)">▶</div>
                    <button type="button" class="scan" @click="deleteComment(sp.vid,yh.id)">X</button>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
    export default {
        name: "YonHulikesp",
        data() {
            return {
                yinyue: [],
                yh: {},
                likeshus: {},
                likeshus2: {}
            }
        },
        methods: {
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    // console.log(resp);
                    // console.log(resp.data.data);
                    this.yh = resp.data.data;
                    this.bianliyy();
                    this.likeshu();
                    this.likeshu2();
                });
            },
            bianliyy() {
                console.log(this.yh.id);
                this.$axios({
                    url: "/api/yhlikesp?id=" + this.yh.id
                }).then(r => {
                    console.log('好好好好');
                    console.log(r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu(){
                this.$axios({
                    url: "/api/yhlikespshu?id=" + this.yh.id
                }).then(r => {
                    console.log(r);
                    this.likeshus = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            likeshu2(){
                this.$axios({
                    url: "/api/yhlikeyyshu?id=" + this.yh.id
                }).then(r => {
                    console.log(r);
                    this.likeshus2 = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            deleteComment(id,yhid) {
                this.$confirm("确定要取消喜欢吗?")
                    .then(() => {
                        this.$axios({
                            url: '/api/yhsclksp?vid=' + id+'&id='+ yhid
                        }).then(r => {
                            this.$message("移除成功!");
                            this.bianliyy();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },

        },
        created() {
            this.doLogin2();
        }
    }
</script>

<style scoped>
    #xhgequ{
        margin-top: 0px;
        /* background-color: #000000; */
        /* border: 1px solid red; */
        width: 100%;
        height: 700px;
        position: relative;
    }
    #ge{
        position: absolute;
        left: 100px;
        top: 0px;
        color: black;
        font-size: 18px;
        font-weight: 100;
    }
    #sp{
        position: absolute;
        left: 200px;
        top: 0px;
        color: #90CD98;
        font-size: 18px;
        font-weight: 100;
    }
    #ge:hover,#sp:hover{
        color: #90CD98;
        cursor: pointer;
    }
    #xian{
        position: absolute;
        width: 90%;
        height: 1px;
        background-color: #90CD98;
        top: 50px;
        left: 70px;
    }
    #yy{
        position: absolute;
        left: 160px;
        top: 60px;
        color: gray;
        font-size: 15px;
        font-weight: 100;
    }
    #zz{
        position: absolute;
        left: 570px;
        top: 60px;
        color: gray;
        font-size: 15px;
        font-weight: 100;
    }



    #yyk{				/* 以这个为锚点 */
        width: 95%;
        height: 300px;
        /* background-color: red; */
        position: absolute;
        left: 50px;
        top: 70px;
        /* margin-left: 50px;
        margin-top: 50px; */
    }
    .yykuai{
        position: relative;
        margin-left: 20px;
        margin-bottom: 10px;
        width: 220px;
        height: 190px;
        background-color: #F6F6F6;
        float: left;
        /* border: 1px solid black; */
    }
    .yytp{
        /* background-color: red; */
        width: 220px;
        /* height: 220px; */
        margin-bottom: 7px;
        border-radius: 10px;
    }
    .yyzz{
        color: gray;
    }
    .yyname:hover,.yyzz:hover{
        color: #90CD98;
    }

    .buofan{
        width: 35px;
        height: 45px;
        background-color: white;
        font-size: 25px;
        border-radius: 50px;
        position: absolute;
        top: 35px;
        left: 80px;
        padding-left: 20px;
        padding-top: 10px;
    }
    .buofan:hover{
        box-shadow: 1px 1px 10px white;
        cursor: pointer;
    }
    #xiaozi{
        position: absolute;
        top: 80px;
        left: 430px;
    }
    #xiaozi span{
        margin-right: 50px;
    }
    #xiaozi span:hover{
        color: #90CD98;
    }

    .scan{
        position: absolute;
        right: 0px;
        top: 1px;
        width: 25px;
        height: 25px;
        color: #90CD98;
        border: none;
        font-weight: 900;
        border-radius: 30px;
    }
    .scan:hover{
        /* background-color: #90CD98; */
        color: orangered;
        cursor: pointer;
    }
</style>