import VueRouter from "vue-router";
import yhden from "./components/yhden";  //引入组件
import yhden2 from "./comfuyon2/yhden2";
import idnex from "./compone1/idnex";
import vip from "./compone1/vip";
import KanYinyue from "./compone1/KanYinyue";
import YonHu from "./compone1/YonHu";
import KanShipin from "./compone1/KanShipin";
import KanShipinDiergeo from "./compone1/KanShipinDiergeo";
import YonHu2 from "./comfuyon2/YonHu2";
import YonHu3fh from "./comfuyon2/YonHu3fh";
import cazaogeshou from "./compone1/cazaogeshou";
import KanGeshou from "./compone1/KanGeshou";
import YonHu4 from "./comfuyon2/YonHu4";
import cazaoshipin from "./compone1/cazaoshipin";
import cazaoyinyue from "./compone1/cazaoyinyue";
import YonHu5 from "./comfuyon2/YonHu5";
import YonHu6 from "./comfuyon2/YonHu6";

// 返回路由对象
export default new VueRouter({
    routes: [
        {
            path: '/', redirect: 'idnex'		//设置默认页面
        },
        {
            path: "/yhden", component: yhden
        },
        {
            path: "/yhden2", component: yhden2
        },
        {
            path: "/idnex", component: idnex
        },
        {
            path: "/vip", component: vip
        },
        {
            path: "/KanYinyue/:mid", component: KanYinyue
        },
        {
            path: "/YonHu", component: YonHu
        },
        {
            path: "/KanShipin/:vid", component: KanShipin
        },
        {
            path: "/KanShipinDiergeo/:vid", component: KanShipinDiergeo
        },
        {
            path: "/YonHu2", component: YonHu2
        },
        {
            path: "/YonHu3fh", component: YonHu3fh
        },
        {
            path: "/cazaogeshou", component: cazaogeshou
        },
        {
            path: "/KanGeshou/:pid", component: KanGeshou
        },
        {
            path: "/YonHu4", component: YonHu4
        },
        {
            path: "/cazaoshipin", component: cazaoshipin
        },
        {
            path: "/cazaoyinyue", component: cazaoyinyue
        },
        {
            path: "/YonHu5", component: YonHu5
        },
        {
            path: "/YonHu6", component: YonHu6
        },
        {
            path: "/cazaoyinyue/:yyname", component: cazaoyinyue
        },

    ]
})