        <!--                        <img :src=" '/api/yytp/' +gsyy.mtp" class="yytp">-->
        <!--                        <h1 class="blyy" @click="$router.push('/KanYinyue/' + gsyy.mid)">{{gsyy.mname.substring(0,gsyy.mname.indexOf('.'))}}</h1>-->
        <!--                        <h1 class="blzz">{{gsyy.mauthor}}</h1>-->
        <!--                        <div class="ycbf">-->
