<script>
    import hands from '../components/hands';
    import handtiao2 from '../comfuyon2/handtiao2';
    import yhfankui from '../compone1/yhfankui';
    import fanhuidinbu from '../compone1/fanhuidinbu';

    export default {
        name: "KanGeshou",
        components: {
            hands, handtiao2, yhfankui, fanhuidinbu
        },
        data() {
            return {
                yyc: {id: null, name: null, tx: null },
                yy: {},
                yh: {},
                yhsfvip: {},
                yhsf: '有呀',
                likeshus: {},
                likeshus2: {},
                likeshus3: {},
                yinyue: {},
                shipin: {},
                kanshipin : false
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/yhkangs?pid=" + this.yyc.id
                }).then(r => {
                    console.log(r);
                    this.yy = r.data.data;
                    this.kanlikeshu();
                    this.kanlikeshu2();
                    this.kanlikeshu3();
                    this.bianliyy();
                    this.bianlisp();
                }).catch(e => {
                    this.$alert(e);
                });
            },
            bianliyy() {
                console.log(this.yh.id);
                this.$axios({
                    url: "/api/gsyinyue?name=" + this.yy.pname
                }).then(r => {
                    console.log('好好好好');
                    console.log(r);
                    this.yinyue = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            bianlisp() {
                console.log(this.yh.id);
                this.$axios({
                    url: "/api/gsshipin?name=" + this.yy.pname
                }).then(r => {
                    console.log('好好好好');
                    console.log(r);
                    this.shipin = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            kanlikeshu(){
                this.$axios({
                    url: "/api/kangsyyshu?name=" + this.yy.pname
                }).then(r => {
                    console.log(r);
                    this.likeshus = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            kanlikeshu2(){
                this.$axios({
                    url: "/api/kangsspshu?name=" + this.yy.pname
                }).then(r => {
                    console.log(r);
                    this.likeshus2 = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            doLogin2() {
                this.$axios({
                    url: "/api/pd2",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    if (resp.data.data!=404){
                        this.yh = resp.data.data;
                        this.czvip()
                    }else {
                        this.yhsf = null
                    }

                });
            },
            czvip(){
                var formData = new FormData();
                formData.append("name", this.yh.name);

                // console.log(this.yh.name);

                this.$axios({
                    url: '/api/cazvip',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.yhsfvip = r.data.data.vip;
                    // alert('有')

                }).catch(e => {
                    // alert('错误');
                });
            },

            tjsc(){
                if(this.yhsf==null){
                    // this.$alert("系统未检测到账户~");
                    this.$message({
                        showClose: true,
                        message: '系统未检测到账户~',
                        type: 'warning'
                    });
                    return;
                }
                let data = new URLSearchParams();
                data.append("yhid", this.yh.id);
                data.append("gsid", this.yy.pid);
                data.append("gsname", this.yy.pname);
                data.append("gstp", this.yy.ptp);
                data.append("gslike", this.yy.plike);
                data.append("gsjs", this.yy.pjiesao);
                this.$axios({
                    method: "post",
                    url: "/api/tjyhlkgs",
                    data: data
                }).then(r => {
                    this.doLogin();
                    this.dzgs();
                    // this.$message("已关注");
                    this.$message({
                        showClose: true,
                        message: '已关注',
                        type: 'success',
                        offset: 200
                    });
                }).catch(e => {
                    this.$alert(e);
                });
            },
            dzgs(){
                this.$axios({
                    url: "/api/dzgs?pid=" + this.yyc.id
                }).then(r => {
                    console.log(111)
                }).catch(e => {
                    this.$alert(e);
                });
            },
            kanlikeshu3(){
                this.$axios({
                    url: "/api/likeshugs?pid=" + this.yy.pid
                }).then(r => {
                    console.log(r);
                    this.likeshus3 = r.data.data;
                }).catch(e => {
                    this.$alert(e);
                });
            },
            tisi(){
                // this.$alert("亲，你还没有登录哦~");
                this.$message({
                    showClose: true,
                    message: '亲，你还没有登录哦~',
                    type: 'warning'
                });
            },
            zhuanshipin(){
                this.kanshipin = true
            },
            zhuanyinyue(){
                this.kanshipin = false
            }

        },
        created() {
            this.yyc.id = this.$route.params["pid"];
            console.log(this.yyc.id);
            this.doLogin();
            this.doLogin2();
            window.scrollTo({
                top: 0,
                behavior: "instant"
                // 瞬间置顶
            });
        }
    }
</script>

